<template>
  <div class="wdShouyi">
    <ul class="tab_list">
      <li @click="changedt()">
        <i :class="(searchInfo.sort===1||searchInfo.sort===2)? 'active' : ''">成交</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===1 ? 'active' : ''"></span>
          <span class="iconfont icon-xiasanjiao" :class="searchInfo.sort===2 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="changemn()">
        <i :class="(searchInfo.sort===3||searchInfo.sort===4) ? 'active' : ''">签约</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===3 ? 'active' : ''"></span>
          <span class="iconfont  icon-xiasanjiao" :class="searchInfo.sort===4 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="changeyu()">
        <i :class="(searchInfo.sort===5||searchInfo.sort===6) ? 'active' : ''">预约</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===5 ? 'active' : ''"></span>
          <span class="iconfont  icon-xiasanjiao" :class="searchInfo.sort===6 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="popupVisible=true"><i>筛选</i> </li>
    </ul>
    <ul class="jf_list">
      <li v-for="(item,index) in jflist" :key="index">
        <div class="jf_detail">
          <div class="jfd_good">
            {{item.name}} &nbsp;({{item.leader_name}})
          </div>
        </div>
        <div class="jf_data">
          <div class="jfd_type "><span>预约{{item.ins_person_num}}</span><span>签约{{item.qian_num}}</span><span>成交{{item.money|moneyType}}</span></div>
        </div>
      </li>
    </ul>
    <mt-popup
      v-model="popupVisible"
      position="right"
    >
      <div class="po_warp">
        <form @submit.prevent="popupVisible=false;getIncome()">
          <div class="bottom_type">
            <van-button type="default" native-type="submit">搜索</van-button>
          </div>
          <div class="info">精确查找</div>
          <div class="wsearch_area">
            <form action="/">
              <van-search
                v-model="searchInfo.key"
                placeholder="请输入搜索关键词"
                @search="getIncome()"
              />
            </form>
          </div>
        </form>
      </div>
    </mt-popup>
    <a href="" class="kefu iconfont icon-kefu"></a>
  </div>
</template>

<script>
  import { apiGet } from "@/common/js/api";
  import { apiPost } from "@/common/js/api";
  export default {
        name: "MyDistrubIncome",
      data() {
        let query = this.$route.query;
        return {
          show: false,
          lowDate:false,
          searchInfo:{
            grade:query.grade,
            sort:"",
            // timeSort:-1,//0表示时间正序，1倒序,-1表示不参加条件
            // scoreSort:-1,//0表示积分正序，1倒序， -1表示不参加条件,
            // yuscoreSort:-1,//0表示预约正序，1倒序， -1表示不参加条件
          },
          popupVisible: false,
          jflist:[],
        }
      },
      created(){
        this.getIncome();
      },
      methods:{
        handleConfirmone (date) {
          console.log(date);
          this.$set(this.searchInfo,'lowtime',date.toLocaleDateString().replace(/\//g,"-"))
        },
        //结束时间
        handleConfirmtwo (date) {
          console.log(date);
          this.$set(this.searchInfo,'hightime',date.toLocaleDateString().replace(/\//g,"-"))
        },
        //获取收益列表
        getIncome(){
          console.log(this.searchInfo)
          apiGet("/user/grade-list",{
            ...this.searchInfo,
            accessToken:this.$cookie.get('accessToken')
          }).then(res=>{
            this.jflist = res.data.data;
          })
        },
        changedt(){
          const { sort } = this.searchInfo
          this.$set(this.searchInfo,'sort', sort===1? 2 : 1)
          this.getIncome()
        },
        //重置搜索条件
        resetSearch(){
          this.popupVisible = false;
          this.searchInfo = {
            timeSort:-1,//0表示时间正序，1倒序,-1表示不参加条件
            scoreSort:-1,//0表示积分正序，1倒序， -1表示不参加条件
            yuscoreSort:-1,//0表示预约正序，1倒序， -1表示不参加条件
          };
          this.getIncome();
        },
        //按签约分数该地排序
        changemn(){
          const { sort } = this.searchInfo
          this.$set(this.searchInfo,'sort', sort===3? 4 : 3)
          this.getIncome()
        },
        //按预约分数排序
        changeyu(){
          const { sort } = this.searchInfo
          this.$set(this.searchInfo,'sort', sort===5? 6 : 5)
          this.getIncome()
        },
      }
    }
</script>

<style  scoped lang="scss">
  .van-search__action{
    display: none;
  }
  .van-search{
    width: 97%;
  }
  .active{
    color: #3C7FEE !important;
  }
  .wsearch_area{
    width: 100%;
    background: rgb(242, 242, 242);
  }
  .wdShouyi{
    padding-bottom: 0.6rem;
    background: #E6E9ED;
    height: 100%;
    .mint-popup-right{
      width: 70%;
      height: 100%;
      background: white;
      .po_warp{
        position: relative;
        height: 100%;
        overflow: auto;
        .info{
          height: 0.31rem;
          background: #efefef;
          line-height: 0.31rem;
          text-indent: 0.13rem;
          font-size: 0.15rem;
          color: #9a9a9a;
        }
        .time_area,.money_area{
          padding-top: 0.21rem;
          overflow: hidden;
          padding-bottom: 0.2rem;
          span{float: left;margin: 0.15rem 0.06rem;width: 0.16rem;height: 1px;background: #bcbcbc}
          .low_time{margin-left: 5%}
          .low_time,.high_time,input{
            width: 40%;
            font-size: 0.13rem;
            color: #9a9a9a;
            line-height: 0.31rem;
            border:1px solid rgba(188,188,188,1);
            border-radius:0.04rem;
            height: 0.31rem;
            text-indent: 0.09rem;
            float: left;
            i{float: right;color: #3C7FEE;font-size: 0.15rem;margin-right: 0.02rem}
          }
          input:nth-of-type(1){
            margin-left: 5%;
          }
        }
        .type_list{
          display: flex;
          padding-bottom: 0.3rem;
          flex-wrap: wrap;
          li{
            width: 42%;
            height: 0.5rem;
            color: #333;
            font-size: 0.17rem;
            margin-left: 0.13rem;
            margin-top: 0.2rem;
            line-height: 0.5rem;
            text-align: center;
            background:rgba(229,232,235,0.5);
            border-radius:0.04rem;
          }
        }
        .bottom_type{
          position: fixed;
          bottom: 0;
          display: flex;
          width: 100%;
          font-size: 0.19rem;
          height: 0.47rem;
          button{
            flex: 1;
            text-align: center;
            line-height: 0.47rem;
          }
          button:nth-of-type(1){
            color: white;
            position: relative;
            top:.05rem;
            background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
          }
        }
      }

    }
    .tab_list{
      display: flex;
      justify-content: space-between;
      height:0.47rem;
      width: 100%;
      background: white;
      li{flex: 1;line-height: 0.47rem;text-align: center;font-size: 0.19rem;
        display: flex;
        justify-content: center;
        i{font-style: normal}
        div{
          width: 0.3rem;
          display: flex;
          flex-direction: column;
          line-height: 0.34rem;
          span{
            font-size: 0.2rem;
            display: block;
            width: 0.3rem;
            color: #bcbcbc;
            height: 0.1rem;
          }
          .icon-shangsanjiao{
            font-size: 0.16rem;
          }

        }
      }
    }
    .jf_list{
      width: 3.5rem;
      background: white;
      margin: 0.17rem auto;
      border-radius: 0.04rem;
      li{
        border-bottom: 1px solid #f5f5f5;
        width: 3.42rem;
        height:0.8rem;
        margin: 0 auto;
        .jfd_type{
           padding-left: 0.12rem;
          span{
            margin-right: 0.18rem;
            line-height: 0.42rem;
            color:#9A9A9A;
            font-size: .15rem;
          }
        }
        .jf_detail,.jf_data,.jfd_type{
          display: flex;
          justify-content: space-between;
          .jfd_good{
            margin-top: 0.15rem;
            margin-left: 0.1rem;
          }
          .jfd_good,.jfd_year{
            font-size:.19rem;
            font-weight:400;
            color:rgba(51,51,51,1);
            margin-left: 0.12rem;
          }
          .jfd_year{
            font-size: 0.15rem;
            color: #9a9a9a;
            line-height: 0.4rem;
            span{
              margin-left: 0.13rem;
            }
          }
          .jfd_num{line-height: 0.43rem;color: #CC1C1C}
          .jfd_zhuchu{color: #CC1C1C}
          .jfd_num,.jfd_state{width: 30%;text-align: right;font-size: 0.17rem}
          .jfd_state{
            font-size: 0.13rem;
            color: #9a9a9a;
            line-height: 0.34rem;
          }
          .jfd_deal{
            color: #3C7FEE;
          }
        }
      }
    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      right: 0.11rem;
    }
  }
</style>



